.n-wrapper{
    display: flex;
    justify-content: space-around;
    height: 10vh;
    position: relative;
    margin-top: 0px;
}

.n-left{
    flex: 1;
    align-items: center;
    display: flex;
    gap: 2rem;
}
.n-name{
    font-size: 1.3rem;
    font-weight: bold;
    color: var(--orange);
}

.n-right{
    display: flex;
    flex: 1;
    align-items: center;
    justify-content:flex-end;
    font-weight: 400;
}

.n-list{
    display: flex;
    justify-content: flex-end;
}
.n-list > ul{
    display: flex;
    gap: 2rem;
    margin-right: 4rem;
}

.n-list >ul >li{
    list-style: none;
}
.n-list>ul>li>a:hover{
    cursor: pointer;
    color: var(--orange);
}
.n-list>ul>li>a{
    color: black;
    
}
.n-button{
    flex: 2;
}


@media screen and (max-width: 480px){
    /* *{
        margin-left: 0px;
        padding-left: 0px;
    } */
    .n-wrapper{
        display: flex;
        flex-direction: column;
        position: relative;
        /* background-color: aqua;
        height: 10vh; */
    }
    .n-left{
        position: absolute;
        top: 0.5rem;
        left: -1.5rem;
    }

    .n-list > ul{
        position: absolute;
        top: 0.7rem;
        justify-content: center;
        align-items: center;
        display: flex;
        flex-direction: column;
        padding: 10px;
        background-color: rgba(255, 255, 255, 0.957);
        z-index: 10;
        padding: 1.5rem;
        right: -6rem;
        gap: 1.2rem;
    }
    
    .n-list >ul{
        cursor: pointer;
        color: var(--orange);
    }
}