.services {
    display: flex;
    padding: 0 1rem 0 1rem;
    height: 90vh;
    margin-bottom: 8rem;
    margin-top: 7rem;
    margin-bottom: 0rem;
}

.awesome {
    display: flex;
    flex-direction: column;
    flex: 1
}

.awesome>:nth-child(1) {
    color: var(--black);
    font-size: 2.5rem;
    font-weight: bold;
}

.awesome>:nth-child(2) {
    color: var(--orange);
    font-size: 2.5rem;
    font-weight: bold;
}

.awesome>:nth-child(3) {
    color: var(--gray);
    font-size: 1rem;
    margin-top: 1rem;
    text-align: justify;
}

.s-button {
    width: 10rem;
    margin-top: 1rem;
}

.cards {
    flex: 1;
    position: relative;
}

.mern {
    position: absolute;
    left: 19rem;
    top: 2rem;
}
.php{
    position: absolute;
    left: 2rem;
    top: -6rem;   
}


@media screen and (max-width: 480px) {
    .services {
        display: flex;
        padding: 0px;
        height: auto;
        margin: 0 -2rem;
        margin-top: 2rem;
        margin-bottom: 40rem;
        flex-direction: column;
    }

    .awesome {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .awesome>:nth-child(1) {
        color: var(--black);
        font-size: 1.5rem;
        font-weight: bold;
    }

    .awesome>:nth-child(2) {
        color: var(--orange);
        font-size: 1.6rem;
        font-weight: bold;
    }

    .awesome>:nth-child(3) {
        color: var(--gray);
        font-size: 0.8rem;
        margin-top: 1rem;
        text-align: justify;
    }

    .s-button {
        width: 8rem;
        font-weight: bold;
        font-size: 0.6rem;
        margin-top: 1rem;
    }

    .mern {
        position: relative;
        left: -0.4rem;
        top: 24rem;
    }
    .php{
        position: relative;
        left: -0.4rem;
        top: 2rem;   
    }
    .cards{
        position: relative;
    }
}