.intro{
    position: relative;
    display: flex;
    height: 77vh;
    margin-top: 6rem;
}

.i-left{
    display: flex;
    flex:1;
    position: relative;
    flex-direction: column;
    gap: 1.2rem;
}
.i-name{
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
}

.i-name>:nth-child(1){
    color: var(--black);
    font-weight: bold;
    font-size: 3rem;
}
.i-name>:nth-child(2){
    color: var(--orange);
    font-weight: bold;
    font-size: 3rem;
}
.i-name>:nth-child(3){
    color: var(--gray);
    font-weight: 100;
    font-size: 1rem;
}
.i-button{
    width: 7.5rem;
}
.i-icon{
    margin-top: 2rem;
    display: flex;
    gap: 1rem;
}
.i-icon>a>*{
    display: flex;
    width: 2rem;
}
.i-icon>*:hover{
    cursor: pointer;
}


.i-right{
    display: flex;
    flex:1;
    position: relative;
}
.i-right>*{
    position: absolute;
    z-index: 1;
}

.i-right>:nth-child(1){
    transform: scale(1);
    top: -2.3rem;
    left:7rem;
    border: 3px solid var(--orange);
    border-radius: 100%;
}
.float{
    top: 0.8rem;
    right: 35rem;
}

.blur{
    position: absolute;
    width: 22rem;
    height: 14rem;
    border-radius: 50%;
    background: #edd0ff;
    filter: blur(120px);
    z-index: -9;
    top: -10%;
    left: 55%;
}

@media screen and (max-width: 480px){
    .intro{
        flex-direction: column;
        height: auto;
        margin-top: 0rem;
    }
    .i-left{
        display: flex;
        position: relative;
        flex-direction: column;
        gap: 1rem;
        align-items: center;
    }
    .i-name{
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 0.8rem;
        margin: 0 -2rem;
    }
    .i-name>:nth-child(1){
        color: var(--black);
        font-weight: bold;
        font-size: 1.5rem;
    }
    .i-name>:nth-child(2){
        color: var(--orange);
        font-weight: bold;
        font-size: 1.6rem;
    }
    .i-button{
        width: 7.5rem;
    }
    .i-name>:nth-child(3){
        color: var(--gray);
        font-weight: 400;
        font-size: 0.8rem;
        text-align: justify;
    }
    .i-icon{
        margin-top: 0.3rem;
        display: flex;
        gap: 1rem;
    }
    .i-icon>a>*{
        display: flex;
        width: 1.5rem;
    }
    .i-icon>*:hover{
        cursor: pointer;
    }

    .i-right{
        display: flex;
        flex-direction: column-reverse;
        flex:1;
        position: relative;
        align-items: center;

    }
    .float{
        position: static;
        margin-top: 1rem;
    }
    .i-right>:nth-child(1){
        margin-top: 1rem;
        position:static;
        width: 12rem;
        border: 3px solid var(--orange);
        border-radius: 100%;
        margin-bottom: 0rem;
    }
}
