.contact {
    display: flex;
    padding-top: 5rem;
    padding-bottom: 9rem;
}

.c-left {
    flex: 1;
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    align-items: center;
}

.c-left>:nth-child(1) {
    font-size: 2rem;
    color: var(--black);
    font-weight: bold;
}

.c-left>:nth-child(2) {
    font-size: 2.5rem;
    color: var(--orange);
    font-weight: bold;
}

.c-right {
    flex: 2;
}

.c-form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.c-form>:nth-child(1) {
    border: 2px solid var(--orange);
    border-radius: 10px;
    padding: 10px;
    width: 80%;
    outline: none;
    color: var(--lightgray);

}

.c-form>:nth-child(2) {
    border: 2px solid var(--orange);
    border-radius: 10px;
    padding: 10px;
    width: 80%;
    outline: none;
    color: var(--lightgray);

}

.c-form>:nth-child(3) {
    border: none;
    border: 2px solid var(--orange);
    border-radius: 10px;
    padding: 10px;
    width: 80%;
    outline: none;
    color: var(--lightgray);
    height: 4rem !important;

}

.c-form>:nth-child(4) {
    width: 5rem;
    outline: none;
}

@media screen and (max-width: 480px) {
    .contact {
        flex-direction: column;
        padding-top: 3rem;
        padding-bottom: 0rem;
        height: auto;
    }

    .c-left>:nth-child(1) {
        font-size: 1.5rem;
        color: var(--black);
        font-weight: bold;
    }

    .c-left>:nth-child(2) {
        font-size: 1.6rem;
        color: var(--orange);
        font-weight: bold;
    }

    .c-right {
        margin-top: 2rem;
    }

    .c-form {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .c-form>:nth-child(1) {
        border: 2px solid var(--orange);
        border-radius: 10px;
        padding: 7px;
        width: 100%;
        outline: none;
        color: var(--lightgray);

    }

    .c-form>:nth-child(2) {
        border: 2px solid var(--orange);
        border-radius: 10px;
        padding: 7px;
        width: 100%;
        outline: none;
        color: var(--lightgray);

    }

    .c-form>:nth-child(3) {
        border: none;
        border: 2px solid var(--orange);
        border-radius: 10px;
        padding: 7px;
        width: 100%;
        outline: none;
        color: var(--lightgray);
        height: 4rem !important;

    }

    .c-form>:nth-child(4) {
        width: 8rem;
        outline: none;
    }
}