.Portfolio{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 70vh;
    margin-top: -2rem;
    margin-bottom: 7rem;
    gap: 0.4rem;
}
.Portfolio>:nth-of-type(1){
    color: var(--black);
    font-size: 2rem;
    font-weight: bold;
}
.Portfolio>:nth-of-type(2){
    color: var(--orange);
    font-size: 2.5rem;
    font-weight: bold;
}
.project{
    display: flex;
    gap: 2rem;
}
.p-img{
    padding-top: 3rem;
    display: flex;
    align-items: center;
    justify-content: center;
}


.p-div{
    width: 15rem;
    height: 22.5rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
    background: rgba(255,255,255,0.26);
    border: 4px solid var(--orangeCard);
    border-radius: 20px;
    padding: 0px 2px 0px 2px;
    margin-top: 2rem;
}
.p-div>:nth-child(2){
    font-size: 2rem;
    font-weight: bold;
    color: var(--black);
}
.p-div>:nth-child(1){
    width:13rem;
    padding-top: 1rem;
    height: 8rem;
}
.p-div>:nth-child(3){
    font-size: 0.9rem;
    padding: 0 1rem 0 1rem;
    text-align:justify;
}
.p-div>:nth-child(4){
    text-decoration: none;
}

@media screen and (max-width: 480px){
    .Portfolio{
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        height: auto;
        margin-top: 47rem;
        margin-bottom: 0rem;
        gap: 0.4rem;
    }
   
    .Portfolio>:nth-of-type(1){
        font-size: 1.5rem;
    }
    .Portfolio>:nth-of-type(2){
        font-size: 1.6rem;
    }
    .project{
        display: flex;
        flex-direction: column;
        gap: 0rem;
    }
    .p-div{
        height: 23rem;
    }
    .p-div>:nth-child(2){
        font-size: 1.4rem;
        font-weight: bold;
        color: var(--black);
    }
    .p-div>:nth-child(1){
        width:13rem;
        padding-top: 1rem;
        height: 8rem;
    }
    .p-div>:nth-child(3){
        font-size: 0.9rem;
        padding: 0 1rem 0 1rem;
        text-align:justify;
    }
    .p-div>:nth-child(4){
        text-decoration: none;
    }
}
