.card{
    width: 15rem;
    height: 20rem;
    position: absolute;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
    /* text-align: center; */
    background: rgba(255,255,255,0.26);
    border: 4px solid var(--orangeCard);
    border-radius: 20px;
    padding: 0px 2px 0px 2px;
}
.card>img{
    /* transform: scale(0.2rem); */
    padding-top: 1rem;
    width: 10rem;
}
.card>:nth-child(2){
    font-size: 1rem;
    font-weight: bold;
    color: var(--black);
}
.card>:nth-child(3){
    font-size: 0.9rem;
    padding: 0 1rem 0 1rem;
    text-align: justify;
}