.footer {
    display: flex;
    align-items: center;
    margin: 0 -3.5rem;
    position: relative;
    margin-top: -7rem;
    margin-bottom: -1rem;
}

.f-content {
    margin-top: 9rem;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    gap: 1rem;
    color: white;
}
.f-icons{
    display: flex;
    gap: 0.8rem;
}
.imgH{
    width: 100%;
    height: auto;
}
@media screen and (max-width:470px) {
    .footer {
        display: flex;
        align-items: center;
        margin: 0 -3.5rem;
        position: relative;
        margin-top: 0rem;
        margin-bottom: -1rem;
    }
    
    .f-content {
        margin-top: 7rem;
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        width: 100%;
        gap: 1rem;
        color: white;
    }
    .f-icons{
        display: flex;
        gap: 0.8rem;
    }
    .imgH{
        height: 13rem;
    }
}
